'use client';

import React, { useState } from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  ArcElement,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar, Pie } from 'react-chartjs-2';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Select, SelectItem } from '@/components/ui/select';

ChartJS.register(CategoryScale, LinearScale, BarElement, ArcElement, Tooltip, Legend);

const regions = ['Central', 'North', 'South', 'East', 'West'];

const dummyData = {
  Central: {
    labels: ['Party A', 'Party B', 'Party C'],
    counts: [120, 80, 30],
  },
  North: {
    labels: ['Party A', 'Party B', 'Party C'],
    counts: [90, 70, 60],
  },
  South: {
    labels: ['Party A', 'Party B', 'Party C'],
    counts: [150, 120, 50],
  },
  East: {
    labels: ['Party A', 'Party B', 'Party C'],
    counts: [60, 30, 20],
  },
  West: {
    labels: ['Party A', 'Party B', 'Party C'],
    counts: [200, 140, 100],
  },
};

export default function AdminPage() {
  const [region, setRegion] = useState('Central');

  const data = {
    labels: dummyData[region].labels,
    datasets: [
      {
        label: 'Vote Count',
        data: dummyData[region].counts,
        backgroundColor: ['#3b82f6', '#ef4444', '#facc15'],
      },
    ],
  };

  return (
    <div className="min-h-screen bg-gray-100">
      <nav className="bg-white shadow px-6 py-4 flex justify-between items-center">
        <h1 className="text-xl font-bold">VoteX Admin Panel</h1>
        <Button>Host New Vote</Button>
      </nav>

      <main className="p-6">
        <div className="mb-6 flex items-center gap-4">
          <h2 className="text-lg font-semibold">Region:</h2>
          <Select
            value={region}
            onChange={(e) => setRegion(e.target.value)}
          >
            {regions.map((r) => (
              <SelectItem key={r} value={r}>
                {r}
              </SelectItem>
            ))}
          </Select>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <Card>
            <CardContent>
              <h3 className="font-semibold text-lg mb-2">Vote Count - Bar Chart</h3>
              <Bar data={data} />
            </CardContent>
          </Card>

          <Card>
            <CardContent>
              <h3 className="font-semibold text-lg mb-2">Vote Distribution - Pie Chart</h3>
              <Pie data={data} />
            </CardContent>
          </Card>
        </div>

        <div className="mt-8">
          <h3 className="text-lg font-semibold mb-4">Numerical Vote Count</h3>
          <ul className="bg-white shadow p-4 rounded space-y-2">
            {dummyData[region].labels.map((party, idx) => (
              <li key={party} className="flex justify-between">
                <span>{party}</span>
                <span className="font-bold">{dummyData[region].counts[idx]}</span>
              </li>
            ))}
          </ul>
        </div>
      </main>
    </div>
  );
}
